import axios from 'axios';
import Vue from 'vue';
import Vuex from 'vuex';
import router from './router.js'
import App from './App.vue'
import VueSocketio from 'vue-socket.io';
//import vueResource from 'vue-resource'
// import * as uiv from 'uiv'
// Vue.use(uiv)
Vue.use(Vuex);
Vue.prototype.$http = axios;
//Vue.http.options.root = 'http://localhost';
// Vue.config.productionTip = false;
// Vue.http.options.emulateHTTP = true;
/* eslint-disable no-new */
const store = new Vuex.Store({
  state: {
    isConnected: false,
    needInitRaceName: false,
    sortBy: 2,
    di: 1,
    tags: []
  },
  getters: {
    sortedTags: state => {
      var idx = state.sortBy;
      return state.tags.sort(function (a, b) {
        if (a[idx] === b[idx]) return 0;
        return (a[idx] < b[idx]) ? 1 * state.di : -1 * state.di;
      });
    }
  },
  mutations: {
    SOCKET_CONNECT: (state, status) => {
      state.isConnected = true;
      state.needInitRaceName = true;
    },
      SOCKET_DISCONNECT: (state, status) => {
        //console.log('disconnected');
          state.isConnected = false;
      },
    SOCKET_TAGS: (state, tags) => {
      //console.log('new tags!!');
      state.tags = [...state.tags, ...tags];
    },
    SOCKET_CLEARTAGS: (state, data) => {
      //console.log('clear tags!!');
      state.tags = [];
    },
    SOCKET_CURRTAGS: (state, tags) => {
      //console.log('current tags!!');
      state.tags = tags;
    },
    doneInitRaceName(state) {
      state.needInitRaceName = false;
    },
    sortByMatId(state) {
      if (state.sortBy != 0) {
        state.sortBy = 0;
        state.di = 1;
      } else {
        state.di = state.di * -1;
      }
    },
    sortByTagId(state) {
      if (state.sortBy != 1) {
        state.sortBy = 1;
        state.di = 1;
      } else {
        state.di = state.di * -1;
      }
    },
    sortBytStamp(state) {
      if (state.sortBy != 2) {
        state.sortBy = 2;
        state.di = 1;
      } else {
        state.di = state.di * -1;
      }
    }
  },
  actions: {}
})
Vue.use(VueSocketio, 'https://yattaweb.herokuapp.com/', store);
//Vue.use(VueSocketio, 'http://localhost:3000', store);
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  sockets: {},
  components: {
    App
  }
})
// window.App=App;
//window.Vue = Vue;



